footer {
  .nav-footer {
    ul {
      > li::before {
        display: none;
      }
      li {
        @apply px-2;
        a,
        &:visited {
          @apply no-underline text-quicksilver-50;
          &:hover,
          &:focus {
            @apply text-goldRush-200;
          }
        }
      }
    }
  }
}
